<template lang="pug">
.onlineStatus(:class="status ? 'online' : 'offline'")
  b-icon-check.onlineIcon(v-if="status")
</template>

<script>

export default {
  name: 'OnlineStatus',
  props: {
    status: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {}
  },
  methods: {}
}
</script>

<style lang="scss">
.onlineStatus {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  position: absolute;
  bottom: -3px;
  right: -3px;
  border: 2px solid #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  &.online {
    background: #61b610;
    .onlineIcon {
      font-size: 14px;
      color: #fff;
    }
  }
  &.offline {
    background: #f15f6d;
  }
}
</style>